.list-overview {
    margin-bottom: 30px;
  
    &__title {
      margin-top: 110px;
      margin-left: 10px;
      font-size: 1.7rem;
      margin-bottom: 40px;
      text-transform: capitalize;
  
      @media screen and (max-width: 800px) {
        margin-top: 180px;
        text-align: center;
        font-size: 1.4rem;
        margin-left: unset;
      }
    }
  
    &__outer {
      width: 100%;
      display: flex;
      justify-content: center;
    }
  
    &__inner {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
      grid-gap: 30px;
      grid-template-rows: 1fr;
      width: 100%;
      margin: 0 auto;
    }
  }